// Ê®°ÊãüÊùÉÈôêÈÖçÁΩÆÊï∞ÊçÆ
const mockPermissionConfig = {
  modules: Object.values(PERMISSION_MODULES).map(moduleKey => ({
    id: moduleKey,
    name: MODULE_CONFIG[moduleKey].label,
    description: MODULE_CONFIG[moduleKey].description,
    category: MODULE_CONFIG[moduleKey].category,
    icon: MODULE_CONFIG[moduleKey].icon,
    isEnabled: true,
    actions: Object.values(PERMISSION_ACTIONS).map(actionKey => ({
      id: actionKey,
      name: ACTION_CONFIG[actionKey].label,
      description: ACTION_CONFIG[actionKey].description,
      color: ACTION_CONFIG[actionKey].color,
      isEnabled: true,
    })),
  })),
  categories: [
    {
      id: 'Ê†∏ÂøÉÂäüËÉΩ',
      name: 'Ê†∏ÂøÉÂäüËÉΩ',
      description: 'Á≥ªÁªüÊ†∏ÂøÉ‰∏öÂä°ÂäüËÉΩ',
      icon: <Activity className='w-4 h-4' />,
    },
    {
      id: 'ÊúçÂä°ÁÆ°ÁêÜ',
      name: 'ÊúçÂä°ÁÆ°ÁêÜ',
      description: 'ITÊúçÂä°Áõ∏ÂÖ≥ÂäüËÉΩ',
      icon: <Globe className='w-4 h-4' />,
    },
    {
      id: 'ÂàÜÊûêÂ∑•ÂÖ∑',
      name: 'ÂàÜÊûêÂ∑•ÂÖ∑',
      description: 'Êï∞ÊçÆÂàÜÊûêÂíåÊä•Âëä',
      icon: <BarChart3 className='w-4 h-4' />,
    },
    {
      id: 'Á≥ªÁªüÁÆ°ÁêÜ',
      name: 'Á≥ªÁªüÁÆ°ÁêÜ',
      description: 'Á≥ªÁªüÈÖçÁΩÆÂíåÁÆ°ÁêÜ',
      icon: <Settings className='w-4 h-4' />,
    },
  ],
};

const PermissionConfiguration = () => {
  const [permissionConfig, setPermissionConfig] = useState(mockPermissionConfig);
  const [searchTerm, setSearchTerm] = useState('');
  const [categoryFilter, setCategoryFilter] = useState('all');
  const [hasChanges, setHasChanges] = useState(false);
  const [saving, setSaving] = useState(false);
  const [viewMode, setViewMode] = useState<'card' | 'tree'>('card');

  // ËøáÊª§Ê®°Âùó
  const filteredModules = permissionConfig.modules.filter(module => {
    const matchesSearch =
      module.name.toLowerCase().includes(searchTerm.toLowerCase()) ||
      module.description.toLowerCase().includes(searchTerm.toLowerCase());
    const matchesCategory = categoryFilter === 'all' || module.category === categoryFilter;
    return matchesSearch && matchesCategory;
  });

  // ÊåâÂàÜÁ±ªÂàÜÁªÑÊ®°Âùó
  const modulesByCategory = filteredModules.reduce((acc: Record<string, any[]>, module) => {
    if (!acc[module.category]) {
      acc[module.category] = [];
    }
    acc[module.category].push(module);
    return acc;
  }, {});

  // Â§ÑÁêÜÊ®°ÂùóÁä∂ÊÄÅÂàáÊç¢
  const handleToggleModule = (moduleId: string) => {
    setPermissionConfig(prev => ({
      ...prev,
      modules: prev.modules.map(module =>
        module.id === moduleId ? { ...module, isEnabled: !module.isEnabled } : module
      ),
    }));
    setHasChanges(true);
  };

  // Â§ÑÁêÜÊìç‰ΩúÁä∂ÊÄÅÂàáÊç¢
  const handleToggleAction = (moduleId: string, actionId: string) => {
    setPermissionConfig(prev => ({
      ...prev,
      modules: prev.modules.map(module =>
        module.id === moduleId
          ? {
              ...module,
              actions: module.actions.map(action =>
                action.id === actionId ? { ...action, isEnabled: !action.isEnabled } : action
              ),
            }
          : module
      ),
    }));
    setHasChanges(true);
  };

  // ÊâπÈáèÊìç‰Ωú
  const handleBatchToggle = (category: string, enabled: boolean) => {
    setPermissionConfig(prev => ({
      ...prev,
      modules: prev.modules.map(module =>
        module.category === category ? { ...module, isEnabled: enabled } : module
      ),
    }));
    setHasChanges(true);
  };

  // ‰øùÂ≠òÈÖçÁΩÆ
  const handleSave = async () => {
    setSaving(true);
    try {
      await new Promise(resolve => setTimeout(resolve, 1500));
      setHasChanges(false);
      message.success('ÊùÉÈôêÈÖçÁΩÆ‰øùÂ≠òÊàêÂäüÔºÅ');
    } catch (error) {
      message.error('‰øùÂ≠òÂ§±Ë¥•ÔºåËØ∑ÈáçËØïÔºÅ');
    } finally {
      setSaving(false);
    }
  };

  // ÈáçÁΩÆÈÖçÁΩÆ
  const handleReset = () => {
    setPermissionConfig(mockPermissionConfig);
    setHasChanges(false);
    message.info('ÈÖçÁΩÆÂ∑≤ÈáçÁΩÆ');
  };

  // ÁªüËÆ°‰ø°ÊÅØ
  const stats = {
    totalModules: permissionConfig.modules.length,
    enabledModules: permissionConfig.modules.filter(m => m.isEnabled).length,
    totalActions: permissionConfig.modules.reduce((sum, module) => sum + module.actions.length, 0),
    enabledActions: permissionConfig.modules.reduce(
      (sum, module) => sum + module.actions.filter(a => a.isEnabled).length,
      0
    ),
  };

  // ÁîüÊàêÊùÉÈôêÊ†ëÊï∞ÊçÆ
  const generateTreeData = () => {
    return permissionConfig.categories.map(category => ({
      title: (
        <div className='flex items-center justify-between'>
          <span className='flex items-center gap-2'>
            {category.icon}
            <Text strong>{category.name}</Text>
          </span>
          <div className='flex gap-2'>
            <Button size='small' type='link' onClick={() => handleBatchToggle(category.id, true)}>
              ÂÖ®ÈÉ®ÂêØÁî®
            </Button>
            <Button size='small' type='link' onClick={() => handleBatchToggle(category.id, false)}>
              ÂÖ®ÈÉ®Á¶ÅÁî®
            </Button>
          </div>
        </div>
      ),
      key: category.id,
      children:
        modulesByCategory[category.id]?.map((module: any) => ({
          title: (
            <div className='flex items-center justify-between w-full'>
              <div className='flex items-center gap-2'>
                <span>{module.icon}</span>
                <span>{module.name}</span>
                <Switch
                  size='small'
                  checked={module.isEnabled}
                  onChange={() => handleToggleModule(module.id)}
                />
              </div>
            </div>
          ),
          key: module.id,
          children: module.actions.map((action: any) => ({
            title: (
              <div className='flex items-center justify-between w-full'>
                <Tag color={action.color}>{action.name}</Tag>
                <Switch
                  size='small'
                  checked={action.isEnabled}
                  onChange={() => handleToggleAction(module.id, action.id)}
                />
              </div>
            ),
            key: `${module.id}-${action.id}`,
          })),
        })) || [],
    }));
  };

  // Ê∏≤ÊüìÊùÉÈôêÂç°ÁâáËßÜÂõæ
  const renderCardView = () => (
    <div className='space-y-6'>
      {permissionConfig.categories.map(category => {
        const categoryModules = modulesByCategory[category.id] || [];
        if (categoryModules.length === 0) return null;

        return (
          <Card
            key={category.id}
            title={
              <div className='flex items-center justify-between'>
                <div className='flex items-center gap-2'>
                  {category.icon}
                  <span>{category.name}</span>
                  <Badge count={categoryModules.length} color='blue' />
                </div>
                <Space>
                  <Button
                    size='small'
                    type='link'
                    onClick={() => handleBatchToggle(category.id, true)}
                  >
                    ÂÖ®ÈÉ®ÂêØÁî®
                  </Button>
                  <Button
                    size='small'
                    type='link'
                    onClick={() => handleBatchToggle(category.id, false)}
                  >
                    ÂÖ®ÈÉ®Á¶ÅÁî®
                  </Button>
                </Space>
              </div>
            }
            className='enterprise-card'
          >
            <Row gutter={[16, 16]}>
              {categoryModules.map((module: any) => (
                <Col xs={24} md={12} lg={8} key={module.id}>
                  <Card
                    size='small'
                    className='h-full'
                    title={
                      <div className='flex items-center justify-between'>
                        <div className='flex items-center gap-2'>
                          <span>{module.icon}</span>
                          <span className='text-sm'>{module.name}</span>
                        </div>
                        <Switch
                          size='small'
                          checked={module.isEnabled}
                          onChange={() => handleToggleModule(module.id)}
                        />
                      </div>
                    }
                  >
                    <Text type='secondary' className='text-xs mb-3 block'>
                      {module.description}
                    </Text>
                    <div className='space-y-2'>
                      {module.actions.map((action: any) => (
                        <div key={action.id} className='flex items-center justify-between'>
                          <Tooltip title={action.description}>
                            <Tag
                              color={action.isEnabled ? action.color : 'default'}
                              className='text-xs cursor-help'
                            >
                              {action.name}
                            </Tag>
                          </Tooltip>
                          <Switch
                            size='small'
                            checked={action.isEnabled}
                            onChange={() => handleToggleAction(module.id, action.id)}
                            disabled={!module.isEnabled}
                          />
                        </div>
                      ))}
                    </div>
                  </Card>
                </Col>
              ))}
            </Row>
          </Card>
        );
      })}
    </div>
  );

  return (
    <div className='p-6'>
      {/* È°µÈù¢Ê†áÈ¢ò */}
      <div className='mb-6'>
        <Title level={2} className='!mb-2'>
          <Key className='inline-block w-6 h-6 mr-2' />
          ÊùÉÈôêÈÖçÁΩÆÁÆ°ÁêÜ
        </Title>
        <Text type='secondary'>ÈÖçÁΩÆÁ≥ªÁªüÂäüËÉΩÊ®°ÂùóÂíåÊìç‰ΩúÊùÉÈôêÔºåÂÆö‰πâËÆøÈóÆÊéßÂà∂Á≠ñÁï•</Text>
      </div>

      {/* ÁªüËÆ°Âç°Áâá */}
      <Row gutter={[16, 16]} className='mb-6'>
        <Col xs={24} sm={12} lg={6}>
          <Card className='enterprise-card'>
            <Statistic
              title='ÂäüËÉΩÊ®°Âùó'
              value={stats.enabledModules}
              suffix={`/ ${stats.totalModules}`}
              prefix={<Layers className='w-5 h-5' />}
              valueStyle={{ color: '#1890ff' }}
            />
          </Card>
        </Col>
        <Col xs={24} sm={12} lg={6}>
          <Card className='enterprise-card'>
            <Statistic
              title='Êìç‰ΩúÊùÉÈôê'
              value={stats.enabledActions}
              suffix={`/ ${stats.totalActions}`}
              prefix={<Activity className='w-5 h-5' />}
              valueStyle={{ color: '#52c41a' }}
            />
          </Card>
        </Col>
        <Col xs={24} sm={12} lg={6}>
          <Card className='enterprise-card'>
            <Statistic
              title='ÂêØÁî®Ê®°Âùó'
              value={((stats.enabledModules / stats.totalModules) * 100).toFixed(1)}
              suffix='%'
              prefix={<CheckCircle className='w-5 h-5' />}
              valueStyle={{ color: '#722ed1' }}
            />
          </Card>
        </Col>
        <Col xs={24} sm={12} lg={6}>
          <Card className='enterprise-card'>
            <Statistic
              title='ÊùÉÈôêË¶ÜÁõñÁéá'
              value={((stats.enabledActions / stats.totalActions) * 100).toFixed(1)}
              suffix='%'
              prefix={<Shield className='w-5 h-5' />}
              valueStyle={{ color: '#fa8c16' }}
            />
          </Card>
        </Col>
      </Row>

      {/* ÈÖçÁΩÆÂèòÊõ¥ÊèêÈÜí */}
      {hasChanges && (
        <Alert
          message='ÈÖçÁΩÆÂ∑≤‰øÆÊîπ'
          description='ÊÇ®ÊúâÊú™‰øùÂ≠òÁöÑÊùÉÈôêÈÖçÁΩÆÊõ¥ÊîπÔºåËØ∑ÂèäÊó∂‰øùÂ≠ò„ÄÇ'
          type='warning'
          showIcon
          closable
          className='mb-6'
        />
      )}

      {/* ÊêúÁ¥¢ÂíåÊìç‰ΩúÊ†è */}
      <Card className='mb-6'>
        <Row gutter={[16, 16]} align='middle'>
          <Col xs={24} md={8}>
            <Input
              placeholder='ÊêúÁ¥¢Ê®°ÂùóÊàñÊùÉÈôê...'
              prefix={<Search className='w-4 h-4 text-gray-400' />}
              value={searchTerm}
              onChange={e => setSearchTerm(e.target.value)}
              allowClear
            />
          </Col>
          <Col xs={24} md={6}>
            <Select
              placeholder='Á≠õÈÄâÂàÜÁ±ª'
              value={categoryFilter}
              onChange={setCategoryFilter}
              style={{ width: '100%' }}
            >
              <Option value='all'>ÂÖ®ÈÉ®ÂàÜÁ±ª</Option>
              {permissionConfig.categories.map(category => (
                <Option key={category.id} value={category.id}>
                  {category.name}
                </Option>
              ))}
            </Select>
          </Col>
          <Col xs={24} md={4}>
            <Select
              placeholder='ËßÜÂõæÊ®°Âºè'
              value={viewMode}
              onChange={setViewMode}
              style={{ width: '100%' }}
            >
              <Option value='card'>Âç°ÁâáËßÜÂõæ</Option>
              <Option value='tree'>Ê†ëÂΩ¢ËßÜÂõæ</Option>
            </Select>
          </Col>
          <Col xs={24} md={6} className='text-right'>
            <Space>
              <Button icon={<RefreshCw className='w-4 h-4' />} onClick={handleReset}>
                ÈáçÁΩÆ
              </Button>
              <Button
                type='primary'
                icon={<Save className='w-4 h-4' />}
                loading={saving}
                onClick={handleSave}
                disabled={!hasChanges}
              >
                {saving ? '‰øùÂ≠ò‰∏≠...' : '‰øùÂ≠òÈÖçÁΩÆ'}
              </Button>
            </Space>
          </Col>
        </Row>
      </Card>

      {/* ÊùÉÈôêÈÖçÁΩÆÂÜÖÂÆπ */}
      <Card className='enterprise-card'>
        {viewMode === 'card' ? (
          renderCardView()
        ) : (
          <Tree
            treeData={generateTreeData()}
            defaultExpandAll
            showLine
            className='enterprise-tree'
          />
        )}
      </Card>
    </div>
  );
};

export default PermissionConfiguration;
